$breakpoint: 900;

// 
$gradient: linear-gradient(0deg, rgba(229,85,31,1) 0%, rgba(222,6,108,1) 100%);

// hover fade
$fadeDuration: 0.3s;

// hover fade 
$fadeOpacity: 0.4;

// pc
$pcMargin: 280;

// sp
$spMargin: 20;

// z-index10 
// zIndex(header); 
$zIndex: (
	cursor: 30,
	movieModal: 20,
  headerNav: 12,
	header: 11,
	footer: 10,
);

@function zIndex($key) {
  @return map-get($zIndex, $key);
}

